import SolverService from './SolverService'

const EPSILON_TEST_CASES = [
    [
    'S-aBaA',
    'S-BYXAa',
    'B-XCAAX',
    'B-abBS',
    'X-abX',
    'X-',
    'A-bSX',
    'A-',
    'C-XAX',
    'C-abXA',
    'Y-ВAXaB',
    'Y-abXA'
],
    [
        'S-aBA',
        'S-BSXA',
        'B-XCAX',
        'B-BYa',
        'B-b',
        'X-abX',
        'X-',
        'A-ASbX',
        'A-AX',
        'A-',
        'C-XAAX',
        'Y-BAXB',
        'Y-abXA',
    ]
];

class EpsilonSetSolver extends SolverService{

    constructor() {
        super('sp', 'eps-non-terms', 'побудувати множину ε–нетерміналів');
    }


    getTestCase(index) {
        let rules = [];
        for(let rule of EPSILON_TEST_CASES[index]) {
            let parts = rule.split('-');
            rules.push({
                left: parts[0],
                right: parts[1],
            })
        }

        return rules;
    }

    solve(input, stepOutputHandle, answerOutputHandle) {  // vector <string>, return vector <string>
        let transitions = [];            //vector <pair<string, string>> pair: {nt:, word:}
        let res = [];                         //vector <string>

        for(let s of input){
            // nt - non terminal (1 letter), word - result after transition
            let nt = s.substring(0, 1);
            let word = s.substring(2, s.length);

            // isOk == 1 if no small letters in transition
            let isOk = 1;

            for(let i = 0; i < word.length; i++) {
                let it = word.charAt(i);
                if ('a' <= it && it <= 'z') {
                    isOk = 0;
                    break;
                }
            }

            if (isOk === 1) {
                transitions.push({ nt: nt, word: word });
            }
        }

        let goodNt = []; //set <string>
        for(let it of transitions){
            // if epsilon transition
            if (it.word === '') {
                // non terminal is epsilon non terminal
                this.insert(goodNt, it.nt);
            }
        }

        let iteration = 0;
        stepOutputHandle(this.stepResult('<t class="lwc">' + iteration + '</t>', goodNt));

        for (;;) {
            /*let output = [];
            for(let item of goodNt) output.push(item);
            this.log(this.onAlgoStepResult(iteration, output));*/

            let nextNt = []; //set <string>
            for(let item of goodNt) this.insert(nextNt, item);

            for(let it of transitions) {
                if (nextNt.indexOf(it.nt) === -1) {
                    let isOk = 1;

                    for (let itt of it.word) {
                        if (goodNt.indexOf(itt) === -1) {
                            isOk = 0;
                        }
                    }

                    if (isOk === 1) {
                        nextNt.push(it.nt);
                    }
                }
            }

            if (this.eqSet(nextNt, goodNt)) {
                stepOutputHandle(this.stepResult('<t class="lwc">' + (iteration + 1) + '</t>', goodNt));
                answerOutputHandle(this.printSet(goodNt) + ' - множина ε-нетерміналів для вказаної граматики.');

                return goodNt;
            }

            iteration++;
            let additionalSet = [];
            for(let item of nextNt) {
                if(goodNt.indexOf(item) === -1) additionalSet.push(item);
            }
            stepOutputHandle(this.stepResult('<t class="lwc">' + iteration + '</t>', goodNt, additionalSet));

            goodNt = nextNt;
        }
    }

    stepResult(iteration, prevSet, addSet) {
        let output = 'S' + iteration + ' = ' + this.printSet(prevSet);

        if(addSet !== undefined && addSet.length !== 0) {
            output += ' ∪ ' + this.printSet(addSet.toString());
        }

        return output;
    }

    printSet(set) {
        if(set.length > 1)set.sort();
        return '{ ' + set.toString()
                         .replaceAll(',', ', ') + ' }';
    }

    log(msg) {
        console.log('[DEBUG]: ', msg)
    }

    eqSet(as, bs) {
        if(as.size !== bs.size) return false;

        for(let i = 0; i < as.length; i++) {
            if(bs.indexOf(as[i]) === -1 ) return false;
        }

        return true;
    }

    insert(set, item) {
        if(!set.includes(item)) set.push(item);
    }

}

let instance = new EpsilonSetSolver();

export default instance;