<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div class="container">
        <div class="card">
            <div class="card-title">Перепиши умову</div>
            <div class="card-comment" v-html="'Граматика, для якої потрібно ' + solver.getDescription() + ':'"></div>
            <div class="grammar-rules">
                <div v-for="index in rules.length" :key="index" class="grammar-rule-line">
                    <input type="text" v-model="rules[index - 1].left" class="grammar-rule-input grammar-rule-input-left">
                    <b class="grammar-rule-splitter">&#10132;</b>
                    <input type="text" v-model="rules[index - 1].right" class="grammar-rule-input grammar-rule-input-right">
                    <button v-if="rules.length > 2" class="grammar-rule-delete" v-on:click="deleteRule(index)">&#10006;</button>
                </div>
            </div>
            <div class="options-buttons-wrapper">
                <button class="grammar-rule-add" v-on:click="addRule()">+ Ше одна хрінька</button>
                <button class="put-example-button example-1" v-on:click="putExample(0)">Приклад 1</button>
                <button class="put-example-button example-2" v-on:click="putExample(1)">Приклад 2</button>
            </div>
        </div>
        <div class="run-button-wrapper">
            <button v-if="validate()" class="run-button" v-on:click="run">Пошльопали</button>
            <button v-else class="run-button unactivated">Пошльопали</button>
        </div>
        <div class="card">
            <div class="card-title">Що робили</div>
            <div v-for="output in outputBuffer" :key="output"
                 class="solving-steps" v-html="output"></div>
        </div>
        <div class="card">
            <div class="card-title">В результаті</div>
            <div v-html="result"></div>
        </div>
    </div>
</template>
<script>
    import SolverContext from '../service/SolverContext'

    export default {
        data() {
            return {
                rules: [{
                    left: 'S',
                    right: ''
                }, {
                    left: '',
                    right: ''
                }],
                solver: SolverContext.getSolverService(this.topic, this.theme),
                outputBuffer: [],
                result: ''
            }
        },
        props: ['topic', 'theme'],
        computed: {},
        components: {},
        methods: {
            init() {
                this.solver = SolverContext.getSolverService(this.topic, this.theme);
                this.outputBuffer = [];
                this.result = '';
            },
            validate() {
                if(this.rules.length < 2) return false;

                for(let rule of this.rules) {
                    if(rule.left === '') return false;
                }

                return true;
            },
            run() {
                if (this.solver !== undefined) {
                    this.outputBuffer = [];
                    this.result = '';

                    let input = [];
                    for(let rule of this.rules) {
                        input.push(rule.left + '-' + rule.right);
                    }

                    this.solver.solve(input, output => {
                        this.outputBuffer.push(output);
                    }, result => {
                        this.result = result;
                    });
                }
            },
            addRule() {
            this.rules.push({ left: '', right: '' });
            },
            deleteRule(index) {
                this.rules.splice(index - 1, 1);
            },
            putExample(index) {
                this.rules = this.solver.getTestCase(index);
                this.run();
            }
        },
        created() {
            this.init();
        },
        watch: {
            topic() { this.init() },
            theme() { this.init() }
        },
        mounted() {
        }
    }
</script>
<style scoped>
    .container {
        /*background: var(--container-dark);*/
        flex: 1;
        width: 100%;
    }

    .card {
        background: var(--container-dark);
        margin: 20px 0px 20px 0px;
        padding: 15px;
        border-radius: 5px;

        color: var(--font-color-3);
    }

    .card-title {
        margin: 0px 0px 10px 0px;

        color: var(--font-color-1);
        font-size: 18px;
        font-weight: bold;
    }

    .card-comment {
        margin: -5px 0px 10px 0px;

        font-size: 14px;
    }

    .solving-steps {
        margin: 8px 0px 8px 0px;

        line-height: 22px;
    }

    .grammar-rules {
        display: grid;
        grid-template-columns: repeat(3, 33%);
    }

    .grammar-rule-line {
        margin: 10px 0px 10px 0px;
    }

    .grammar-rule-input {
        background: var(--content);
        min-width: 45px;
        padding: 5px 8px 5px 8px;
        border: none;

        font-size: 16px;
        color: var(--font-color-1);
    }

    .grammar-rule-input-left{
        width: 45px;

        text-align: center;
    }

    .grammar-rule-input-right{
        width: 120px;
    }

    .grammar-rule-splitter {
        margin: 0px 10px 0px 10px;
    }

    .grammar-rule-add {
        grid-area: add;
        background: var(--success);
        width: fit-content;
        margin: 15px auto 5px auto;
        padding: 4px 8px 4px 8px;
        border: none;
        border-radius: 3px;
        cursor: pointer;

        color: var(--font-color-1);
        font-size: 18px;
        line-height: 18px;
        font-weight: bold;
    }

    .empty{
        grid-area: empty;
    }

    .grammar-rule-delete {
        background: var(--error);
        margin: 0px 10px 0px 25px;
        padding: 4px 8px 4px 8px;
        border: none;
        border-radius: 3px;
        cursor: pointer;

        color: var(--font-color-1);
    }

    .options-buttons-wrapper{
        display: grid;
        grid-template-columns: max-content auto max-content max-content;
        grid-template-areas: "add empty example-1 example-2";
    }

    .put-example-button{
        background: var(--font-color-2);
        width: fit-content;
        height: fit-content;
        margin: 15px 10px 5px 5px;
        padding: 4px 8px 4px 8px;
        border: none;
        border-radius: 5px;
        cursor: pointer;

        color: var(--font-color-1);
        font-size: 18px;
        font-weight: bold;
    }

    .example-1{
        grid-area: example-1;
    }

    .example-2{
        grid-area: example-2;
    }

    .run-button-wrapper{
        width: fit-content;
        margin: 5px auto 5px auto;
    }

    .run-button{
        background: transparent;
        width: fit-content;
        border: 2px solid var(--lighter-2);
        padding: 4px 8px 4px 8px;
        border-radius: 5px;
        cursor: pointer;

        color: var(--lighter-2);
        font-size: 18px;
        font-weight: bold;
    }

    .run-button:hover {
        background: var(--lighter-2);

        color: var(--content);
        font-weight: bold;
    }

    .unactivated{
        border: 2px solid var(--error);

        color: var(--error);
    }

    .unactivated:hover{
        background: var(--error);
    }

    @media screen and (max-width: 1270px) {
        .grammar-rules {
            grid-template-columns: 50% 50%;
        }
    }

    @media screen and (max-width: 768px) {
        .options-buttons-wrapper{
            display: grid;
            grid-template-columns: auto auto;
            grid-template-areas: "add add" "example-1 example-2";
        }

        .put-example-button {
            margin-left: auto;
            margin-right: auto;
        }

        .grammar-rule-delete{
            margin-left: 20px;
            margin-right: 0px;
        }
    }

    @media screen and (max-width: 678px) {
        .grammar-rules {
            grid-template-columns: 100%;
        }
    }
</style>