import EpsilonSolverService from './EpsilonSetSolver'
import FirstKSetSolver from './FirstKSetSolver'


class SolverContext {

    #solvers = {};

    constructor() {
        this.#solvers[this.buildSolverId(EpsilonSolverService.getTopic(), EpsilonSolverService.getTheme())] = EpsilonSolverService;
        this.#solvers[this.buildSolverId(FirstKSetSolver.getTopic(), FirstKSetSolver.getTheme())] = FirstKSetSolver;
    }

    /*registerSolverService(solverService) {
        if(solverService instanceof SolverService) {
            let solverId = this.buildSolverId(solverService.getTopic(), solverService.getTheme());

            this.#solvers[solverId] = solverService;
        }
    }*/

    getSolverService(topic, theme) {
        return this.#solvers[this.buildSolverId(topic, theme)];
    }

    buildSolverId(topic, theme) {
        return '/' + topic + '/' + theme;
    }
}

let instance = new SolverContext();

export default instance;